<script>
import {getCookie} from "../../utils.js"
export default {
    name: "service-agreement",
    data() {
        return{
            isShow: false
        }
    },
    mounted() {
        let lang = getCookie('lang')
        if (lang=='en') {
            this.isShow = true
        } else {
            this.isShow = false
        }
        window.scroll(0,0)
    },
    methods: {
        goBack() {
            this.$router.go(-1)
        }
    }
};
