import axios from 'axios'
import Vue from 'vue'

export default {
  namespaced: true,
  state() {
    return {
      cart: [],
      address: {
        id: null
      }
    }
  },
  getters: {
    getCart (state) {
      // console.log('getcart',state)
      if (state.cart.length <=0 ) {
        let cart = JSON.parse(sessionStorage.getItem('cart'))
        // console.log('cart',cart)
        return state.cart = cart || []
      }
    },
    // 
    getNumInCart: ({cart}) => (id) => {
      let num = 0
      // console.log('NumInCart',cart)
      cart.forEach(item => {
        if (item.data.id === id) num = item.num
      })
      return num
    },
    // 
    totalNum({cart}) {
      let num = 0
      cart.forEach(item => {
        num += item.num
      })
      return num
    },
    // 
    qualityOriginalTotal:({cart}) => (id) => {
      let totalPrice = 0
      cart.forEach(item => {
        // console.log('cart-',item)
        if (item.sellerId === id) {
          if (!item.data.presentPrice) {
            // console.log('',item.data.presentPrice)
            let price = (item.data.originalPrice || 0).mul(item.num)
            totalPrice = totalPrice.add(price)
          }
        }
      })
      return totalPrice
    },
    // 
    originalTotal({cart}) {
      let totalPrice = 0
      cart.forEach(item => {
        let price = (item.data.originalPrice || 0).mul(item.num)
        totalPrice = totalPrice.add(price)
      })
      return totalPrice
    },
    // 
    presentTotal({cart}) {
      let totalPrice = 0
      cart.forEach(item => {
        totalPrice = totalPrice.add(item.price)
      })
      return totalPrice
    }
  },
  mutations: {
    // 
    setCart(state, data) {
      // console.log('setCart',data)
      state.cart = data
    },
    // 
    addToCart({cart}, data) {
      let isExisted = false
      let price = data.presentPrice || data.originalPrice || 0
      // console.log('addToCart',cart,data)
      cart.forEach(item => {
        if (item.data.id === data.id) {
          if (data.maxNum && data.maxNum <= item.num) {
            Vue.prototype.$message.warning(`${data.maxNum}`)
            isExisted = true
            return
          }
          if (data.limitNum !== 0 && data.limitNum) {
            if (data.limitNum && data.limitNum <= item.num) {
              Vue.prototype.$message.warning(`${data.limitNum}`)
              price = data.originalPrice || 0
            }else{
              price = data.presentPrice
            }
          }
          item.num++
          item.price = item.price.add(price)
          isExisted = true
        }
      })
      !isExisted && cart.push({data, num: 1, price})
    },
    // 
    removeFromCart({cart}, data) {
      let price = data.presentPrice || data.originalPrice || 0
      cart.forEach((item, index) => {
        if (item.data.id === data.id) {
          if (item.num === 1) {
            cart.splice(index, 1)
          }
          item.num--
          if (data.limitNum !== 0 && data.limitNum) {
            if (data.limitNum && data.limitNum <= item.num) {
              price = data.originalPrice || 0
            }else{
              price = data.presentPrice || 0
            }
          }
            item.price = price.sub(item.price)
        }
      })
    },
    // 
    clearCart({cart}) {
      cart.splice(0, cart.length)
      // sessionStorage.removeItem('cart')
      // localStorage.removeItem('cart')
    },
    // 
    setAddress(state, data) {
      // console.log(data)
      state.address = data
      console.log('state.address',state.address)
      // state.address.id = data.id
    }
  },
  actions: {
    // 
    async ajaxOrderList(store, data) {
      let { data: res } = await axios.post('/innerService/TheOrderList', data)
      return res
    },
    // // 
    async ajaxOrderDetail(store, data) {
      let { data: res } = await axios.post('/innerService/TheOrderDetails', data)
      return res
    },
    // // 
    async ajaxRefundDetail(store, data) {
      let { data: res } = await axios.post('/innerService/DetailsOfTheRefund', data)
      return res
    },
    // 
    async ajaxApplyRefund(store, data) {
      let {data: res} = await axios.post('/innerService/ToApplyForARefund', data)
      return res
    },
    // // 
    async ajaxPlaceOrder(store, data) {
      let { data: res } = await axios.post('/innerService/orderInfo', data)
      return res
    },
    // 
    async ajaxPayOrder(store, data) {
      let { data: res } = await axios.post('/innerService/addPayOrder', data)
      return res
    },
    // 
    async ajaxAliply(store,data) {
      let res = await axios.post('innerService/zfbPayOrder', data)
      return res
    },
    // 
    async ajaxPaymenWebCat(store,data) {
      let res = await axios.post('/openservice/makeQRCode', data, {
        responseType: 'arraybuffer'
      })
      .then(response => new Buffer(response.data, 'binary').toString('base64'))
      .then(data => {
        return 'data:image/png;base64,'+data
      });
      return res
    },
    // // 
    // async ajaxDeleteOrder(store, data) {
    //   let auth = localStorage.getItem('auth')
    //   let { data: res } = await axios.post('/innerservice/removeOrder', data, JSON.parse(auth))
    //   return res
    // },
    // // 
    async ajaxCancelOrder(store, data) {
      let { data: res } = await axios.post('/innerService/CancelTheOrder', data)
      return res
    },
    // //qiniu token
    async ajaxGetQiniuToken(store, data) {
      let auth = localStorage.getItem('auth')
      let { data: res } = await axios.post('/openservice/getImgToken', null, JSON.parse(auth))
      return res.data;
    },
    // // 
    async ajaxSubmitEvaluate({store}, data) {
      let {data: res} = await axios.post('/innerService/EvaluationOfTheOrder', data)
      return res
    },
    // // 
    async ajaxEvaluateDetail({store}, data) {
      let {data: res} = await axios.post('/innerService/OrderCommentDetailsInfo', data)
      return res
    },
    // // 
    async ajaxComment(store, data) {   
      let {data: res} = await axios.post('/innerService/userComment', data)
      return res
    },
    // 
    async ajaxDelComment(store, data) {
      let {data: res} = await axios.post('/innerService/deleteUserComment', data)
      return res
    },
    // // 
    async ajaxDeliveryDetail({store}, data) {
      let {data: res} = await axios.post('/innerService/DistributionLocation', data)
      return res
    },
    async ajaxPaymentList({store}, data) {
      let {data: res} = await axios.post('/innerService/getPaymentList', data)
      return res
    } 
  }
}



